import styled from 'styled-components'
import{Link as LinkR} from 'react-router-dom'
import{Link as LinkS} from 'react-scroll'

/*
Nav
NavbarContainer
NavLogo
MobileIcon
NavMenu
NavItem
NavLinks
NavBtn
NavBtnLink
*/

export const Nav = styled.nav`
    background: #000;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;
    

    @media screen and (max-width: 960px){
        transition: 0.8s all ease;
    }
`
export const NavbarContainer = styled.div`
    justify-content: flex-end;
    display: flex;
    flex-direction: row;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1100px;
`
export const NavLogo = styled(LinkR)`
    color: #fff;
    justify-self: start;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    letter-spacing: 10px;
    text-decoration: none;
    transform: translate(25%, 50%);
    position: absolute;
    top: 10px;
    left: 0;
    transform: translate(25%, 50%);

    @media screen and (max-width: 768px){
        margin-left: 12px;
        }
`
export const MobileIcon = styled.div`
    display: none;

    @media screen and (max-width: 768px){
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        color: #fff;
    }
`
export const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    margin-right: -24px;

    @media screen and (max-width: 768px){
        display: none;
    }
`
export const NavItem = styled.li`
    height: 80px;
`
export const NavLinks = styled(LinkS)`
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;

 &.active {
    border-bottom: 3px solid #01bf71;
}
`
export const NavBtn = styled.div`
    display: flex;
    align-items: center;

    @media screen and (max-width: 768px){
        display: none;
    }
`
export const NavBtnLink = styled(LinkR)`
    background: #01bf71;
    border-radius: 50px;
    white-space: nowrap;
    margin-left: 32px;
    padding: 10px 22px;
    color: #010606;
    font-size: 16px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;

    &:hover{
        transition: all 0.2s ease-in-out;
        background: #fff;
        color: #010606;
    }
`